.loader-wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
}

@keyframes dance {
  
  0% {
    transform: scaleY(1);
  }

	50%{
    transform: scaleY(3);
	}
  
  100% {
    transform: scaleY(1);
  }
  
}